html,
body {
    margin: 0;
}

main {
    font-family: -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
    font-size: 1.3em;
    margin: 60px auto;
    max-width: 40em;
    padding: 0 20px;
}

h1 {
    margin-bottom: 60px;
    text-align: center;
}

p {
    line-height: 1.3;
    margin-bottom: 1.5em;
}

ul {
    padding-left: 20px;
}

li {
    line-height: 1.3;
    margin-bottom: 8px;
}

small {
    font-size: 0.8em;
}

footer {
    margin: 60px 0;
    text-align: center;
}
